import React, { Component } from "react";
import moment from "moment";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import { Form, Select, Row, Col, Alert, DatePicker, InputNumber } from "antd";

const { Option } = Select;
// const { TextArea } = Input;

class CurrencyRateUpdateForm extends Component {
  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        values.rate_date = values.rate_date.format("YYYY-MM-DD")
        this.props.submit(values);
      }
    });
  };

  render() {
    // console.log(this.props);
    const { getFieldDecorator, getFieldError } = this.props.form;
    const { currencyRate } = this.props;
    const { loading, currencies } = currencyRate;
    const data = currencyRate.currency_rate ? currencyRate.currency_rate : [];
    const used = data.is_used;
    const error = currencyRate.errorField ? currencyRate.errorField : [];
    // console.log(data);

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };
    const tailFormItemLayout = {
      wrapperCol: {
        xs: {
          span: 24,
          offset: 0
        },
        sm: {
          span: 24,
          offset: 0
        }
      }
    };

    return (
      <Form layout="vertical" onSubmit={this.handleSubmit}>
        {used ? (
          <Row style={{ marginBottom: "20px" }}>
            <Col span={24}>
              <Alert
                message={<IntlMessages id="currencyRate.help" />}
                type="warning"
                showIcon
              />
            </Col>
          </Row>
        ) : (
            ""
          )}

        <div className="isoDetailDataFlex" style={{ padding: "0" }}>
          <div className="isoColumn-2 isoFormGroupComp" style={{ padding: "0 15px 0 0" }}>
            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="currencies.pageHeader" />}
            >
              {getFieldDecorator("id_currency", {
                initialValue: data.id_currency,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  }
                ]
              })(
                <Select
                  loading={loading}
                  showSearch
                  optionFilterProp="children"
                  allowClear={true}
                  disabled={true}
                >
                  {currencies.map((data, key) => (
                    <Option key={key} value={data.id_currency}>
                      {data.currency_code + " (" + data.currency_name + ")"}
                    </Option>
                  ))}
                </Select>
              )}
            </Form.Item>

            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="rateDate" />}
              validateStatus={
                error.rate_date || getFieldError("rate_date")
                  ? "error"
                  : null
              }
              help={
                error.rate_date
                  ? error.rate_date
                  : getFieldError("rate_date")
              }
            >
              {getFieldDecorator("rate_date", {
                initialValue: data.rate_date ? moment(data.rate_date, "YYYY-MM-DD") : null,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  }
                ]
              })(
                <DatePicker
                  style={{ width: "100%" }}
                  allowClear={false}
                  // showTime={{ format: "HH:mm:ss" }}
                  // onChange={this.handleChangeStartDate}
                  // onOpenChange={this.handleChangeStartDate}
                  format="DD MMMM YYYY"
                  disabled={true}
                // disabledDate={e => e.valueOf() > moment()}
                />
              )}
            </Form.Item>

            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="rate" />}
            >
              {getFieldDecorator("rate", {
                initialValue: data.rate,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  }
                ]
              })(
                <InputNumber
                  formatter={value =>
                    `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                  }
                  parser={value => value.replace(/\$\s?|(,*)/g, "")}
                  style={{ width: "100%" }}
                  precision={2}
                />
              )}
            </Form.Item>

            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="fiscalRate" />}
            >
              {getFieldDecorator("fiscal_rate", {
                initialValue: data.fiscal_rate,
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  }
                ]
              })(
                <InputNumber
                  formatter={value =>
                    `${value}`.replace(/\B(?=(\d{3})+(?!\d))/g, ",")
                  }
                  parser={value => value.replace(/\$\s?|(,*)/g, "")}
                  style={{ width: "100%" }}
                  precision={2}
                />
              )}
            </Form.Item>

          </div>
        </div>

        <Form.Item {...tailFormItemLayout}>
          <Button
            type="primary"
            htmlType="submit"
            style={{ marginTop: "20px", marginLeft: "50px" }}
            loading={loading}
          >
            <IntlMessages id="button.submit" />
          </Button>
        </Form.Item>
      </Form>
    );
  }
}

const WrappedCurrencyRateUpdateForm = Form.create({ name: "update_currency_rate" })(CurrencyRateUpdateForm);

export default WrappedCurrencyRateUpdateForm;
