const actions = {
  getCurrencyRates: (page, search, filter) => ({
    type: 'GET_CURRENCY_RATES_REQUEST',
    payload: {
      page: page,
      search: search,
      filter: filter
    }
  }),
  createCurrencyRate: () => ({
    type: 'CREATE_CURRENCY_RATE_REQUEST'
  }),
  storeCurrencyRate: data => ({
    type: 'STORE_CURRENCY_RATE_REQUEST',
    payload: { body: data }
  }),
  editCurrencyRate: id_currency_rate => ({
    type: 'EDIT_CURRENCY_RATE_REQUEST',
    payload: { id_currency_rate: id_currency_rate }
  }),
  updateCurrencyRate: (id_currency_rate, data) => ({
    type: 'UPDATE_CURRENCY_RATE_REQUEST',
    payload: {
      id_currency_rate: id_currency_rate,
      body: data
    }
  }),
  showCurrencyRate: id_currency_rate => ({
    type: 'SHOW_CURRENCY_RATE_REQUEST',
    payload: { id_currency_rate: id_currency_rate }
  }),
  deactivateCurrencyRate: id_currency_rate => ({
    type: 'DEACTIVATE_CURRENCY_RATE_REQUEST',
    payload: { id_currency_rate }
  }),
  activateCurrencyRate: id_currency_rate => ({
    type: 'ACTIVATE_CURRENCY_RATE_REQUEST',
    payload: { id_currency_rate }
  }),
  clearSuccess: () => ({
    type: "CLEAR_SUCCESS"
  }),
  clearError: () => ({
    type: "CLEAR_ERROR"
  }),
  clearErrorField: () => ({
    type: "CLEAR_ERROR_FIELD"
  })
};

export default actions;
